<div class="plp"><h1 class="ui header">{{ category}}</h1><div class="ui grid"><div class="ui three wide column"><div class="ui segment form filter-form"><span class="ui header teal">Filters</span><div class="ui header tiny">Type</div><input v-model="filterKeys" value="1" id="s1" type="checkbox"><label for="s1">Atta Maggie</label><input v-model="filterKeys" value="2" id="s2" type="checkbox"><label for="s2">Oats Maggie</label><input v-model="filterKeys" value="3" id="s3" type="checkbox"><label for="s3">maida</label><div class="ui header tiny"> Mealecule</div><input id="s4" type="checkbox" name="example"><label for="s4">Protien</label><input id="s5" type="checkbox" name="example"><label for="s5">Fat</label><input id="s6" type="checkbox" name="example"><label for="s6">Fibre</label></div></div><div class="ui thirteen wide column"> <label class="select-box" aria-label="Sort by"><select v-model="sortKey"><option value="userId">Price low to high</option><option value="completed">Protien</option><option value="title">Fat</option><option value="id">Carb</option><option>Fibre</option></select></label><div class="ui link cards"><div class="card" v-for="p in filteredList" :key="p.id"><div class="image"><img src="https://www.maggi.in/sites/default/files/styles/product_image_desktop_617_900/public/maggi-2minutes-noodles-617x900.png?itok=GgDSaGCE" alt="productimage"></div><div class="content"><div class="header">{{ p.userId }}</div><div class="meta"><router-link to="/pdp"> {{ p.completed }}</router-link></div><div class="description"> {{ p.title}}</div></div></div></div></div></div></div>