<div class="gamearea"><h1 id="score">{{score }} <div class="ui yellow circular label">{{coins}} </div></h1><div id="gameArea"><div id="obst"></div><div class="overlay" v-show="gamepaused"><button class="startbutton ui primary large button" autofocus v-on:click="startGame()">{{ startButton}}</button></div><div id="player" :style="'left:'+player + 'px'"></div></div></div>